/* Styling Study Landing Page */

body {
  background-color: #f8f8f8;
  margin: 0;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

.right {
  position: absolute;
  top: 12%;
  left: 25%;
  width: 50%;
}

.right h1 {
  font-family: "Asul", sans-serif;
  font-size: 3.5em;
  font-weight: normal;
}

.right span {
  font-weight: 500;
}

.right p {
  font-family: "Rubik", sans-serif;
  font-size: 1.1em;
  font-weight: 300;
}

.right .btn-container {
  position: relative;
  margin-top: 1%;
  margin-left: 45%;
}

.right .btn {
  font-family: "Rubik", sans-serif;
  font-size: 1.5em;
  font-weight: normal;
  background-color: #3200ff;
  color: #f8f8f8;
  border: 1.5px solid #3200ff;
  border-radius: 100px;
  padding: 10px 50px;
  position: absolute;
  top: 0px;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}

.right .btn:hover {
  background-color: #f55000;
  border: 1.5px solid #f55000;
  color: white;
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.forms div {
  position: absolute;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.circle {
  top: 20%;
  left: 7%;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  -webkit-animation: circle-move 17s;
          animation: circle-move 17s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.rectangle {
  top: 10%;
  left: 20%;
  -webkit-animation: rotate-rectangle 15s;
          animation: rotate-rectangle 15s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.half-circle {
  top: 15%;
  left: 30%;
  -webkit-animation: half-circle-move 30s;
          animation: half-circle-move 30s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.triangle {
  top: 25%;
  left: 20%;
  -webkit-animation: triangle-move 13s;
          animation: triangle-move 13s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.square {
  top: 55%;
  left: 10%;
  -webkit-animation: square-move 12s;
          animation: square-move 12s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.arc {
  top: 55%;
  left: 30%;
  -webkit-animation: arc-move 10s;
          animation: arc-move 10s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes rotate-rectangle {
  0% {
    -webkit-transform: rotate(0deg) scale(0.8);
            transform: rotate(0deg) scale(0.8);
  }
  50% {
    -webkit-transform: rotate(15deg) scale(0.8);
            transform: rotate(15deg) scale(0.8);
  }
  100% {
    -webkit-transform: rotate(0deg) scale(0.8);
            transform: rotate(0deg) scale(0.8);
  }
}

@keyframes rotate-rectangle {
  0% {
    -webkit-transform: rotate(0deg) scale(0.8);
            transform: rotate(0deg) scale(0.8);
  }
  50% {
